.form-program {
  border: none;
  display: flex;
  padding: 0;
  margin: 0 0 26px 0;
}

@media (min-width: $tablet-width) {
  .form-program {
    margin: 0 0 75px 0;
    border: 1px solid $special_light_grey;
  }
}

@media (min-width: $desktop-width) {
  .form-program {
    margin: 0;
    border: 1px solid $special_light_grey;
  }
}
